body {
  background-color: #003838;
  color: #c1fffe;
  font-size: 1.5em;
  margin: auto;
  font-family: Georgia, 'Times New Roman', Times, serif
}

.navigation {
  width: 100%;
  margin-top: 0;
  padding-top: 0;
  cursor: pointer;
  background-color: #e9ffff;
  position: fixed;
}
nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.navigation nav li {
  display: inline-block;
  text-align: center;
  margin-left: 70px;
}

.navigation nav a {
  text-decoration: none;
  text-transform: uppercase;
}

.navigation nav a:hover {
  color: #002524;
}

.about-me {
  display: inline-block;
  justify-content: space-between;
  flex-direction: row;
  position: relative;
}

.about-me div {
  position:absolute;
}

.self {
  background-color: #e9ffff;
  color: #002524;
  max-width: 40%;
  float: right;
  margin-top: 60px;
  margin-right: 60px;
  padding-left: 5px;
  border-style: solid;
  border-color: #002524;
  border-radius: 10%;
  padding: 10px;
}

